import React, { useState } from 'react';
import loader_cms from '../assets/loader_cms.gif';

const Product = ({ product, requestAddToCartRequest }) => {
    const [loaded, setLoaded] = useState(false);
    let image = product.img_url;
    const loadImage = () => {
        setLoaded(true);
    };
    return (
        <div className="product">
            <img style={{ display: loaded ? 'block' : 'none' }} className="product__image" onLoad={loadImage} src={image} alt={product.name} />
            {!loaded && <img className="product__image--loading" src={loader_cms} alt="loading" />}
            <div className="product__description">
                <div className="product__name">{product.name}</div>
                <div className="product__price_wrapper">
                    <div className="product__price_wrapper__discounted_price">₹{product.price - (product.price * product.discount / 100)}</div>
                    <div className="product__price_wrapper__price">
                        <strike>₹{product.price}</strike>
                    </div>
                    <div className="product__price_wrapper__discount">({product.discount}% OFF)</div>
                </div>
            </div>
            <button className="btn-primary btn-full" onClick={() => requestAddToCartRequest(product.id)}>Add to Cart</button>
        </div>
    );
};

export default Product;